import { DeleteIcon, EditIcon, RepeatIcon } from "@chakra-ui/icons";
import {
  AlertDialog,
  AlertDialogBody,
  AlertDialogContent,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogOverlay,
  Box,
  Button,
  ButtonGroup,
  Checkbox,
  Heading,
  Input,
  List,
  ListItem,
  useDisclosure,
} from "@chakra-ui/react";

export const IncompleteTodos = (props) => {
  const {
    incompleteTodos,
    onChangeUpdateText,
    onClickUpdate,
    onChangeComplete,
    onClickToggleUpdate,
    onClickDeleteIncompleteTodo,
  } = props;

  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <Box
      minH="200px"
      minW="500px"
      border="1px"
      borderRadius="10"
      borderColor="gray.100"
      p={5}
    >
      <Heading textAlign="center" as="h2" size="lg">
        
      </Heading>
      <List spacing={2}>
        {incompleteTodos.map((todo, index) => (
          <ListItem display="flex" justifyContent="space-between" key={index}>
            {todo.isEditable ? (
              <>
                <Input
                  htmlSize={30}
                  variant="outline"
                  value={todo.text}
                  onChange={(e) => onChangeUpdateText(e, index)}
                />
                <Button colorScheme="teal" onClick={() => onClickUpdate(index)}>
                  <RepeatIcon />
                </Button>
              </>
            ) : (
              <>
                <Checkbox
                  isChecked={todo.isChecked}
                  onChange={() => onChangeComplete(index)}
                >
                  {todo.text}
                </Checkbox>
                <ButtonGroup>
                  <Button
                    colorScheme="teal"
                    onClick={() => onClickToggleUpdate(index)}
                  >
                    <EditIcon />
                  </Button>
                  <Button colorScheme="red" onClick={onOpen}>
                    <DeleteIcon />
                  </Button>
                  <AlertDialog isOpen={isOpen} onClose={onClose}>
                    <AlertDialogOverlay>
                      <AlertDialogContent>
                        <AlertDialogHeader fontSize="lg" fontWeight="bold">
                          
                        </AlertDialogHeader>

                        <AlertDialogBody>
                          
                        </AlertDialogBody>

                        <AlertDialogFooter>
                          <Button onClick={onClose}></Button>
                          <Button
                            ml={3}
                            colorScheme="red"
                            onClick={() => {
                              onClickDeleteIncompleteTodo(index);
                              onClose();
                            }}
                          >
                            
                          </Button>
                        </AlertDialogFooter>
                      </AlertDialogContent>
                    </AlertDialogOverlay>
                  </AlertDialog>
                </ButtonGroup>
              </>
            )}
          </ListItem>
        ))}
      </List>
    </Box>
  );
};
